'use client';

import { useState, useTransition } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { toast } from 'sonner';

import { updatePrayerAction } from '@/app/(public)/prayer/actions';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Textarea } from '@/components/ui/textarea';
import { ImageUploader } from '@/components/uploads/image-uploader';
import { prayerUpdateSchema, type PrayerUpdateValues } from '@/lib/validators/prayer';

const defaultValues: PrayerUpdateValues = {
  id: '',
  content: '',
  imageUrl: '',
};

type PrayerEditFormProps = {
  initialValues: PrayerUpdateValues;
};

export function PrayerEditForm({ initialValues }: PrayerEditFormProps) {
  const [serverError, setServerError] = useState<string | null>(null);
  const [isPending, startTransition] = useTransition();

  const form = useForm<PrayerUpdateValues>({
    resolver: zodResolver(prayerUpdateSchema),
    defaultValues: {
      ...defaultValues,
      ...initialValues,
    },
  });

  const handleSubmit = form.handleSubmit((values) => {
    setServerError(null);
    startTransition(async () => {
      const result = await updatePrayerAction(values);

      if (result && result.status === 'error') {
        if (result.fieldErrors) {
          Object.entries(result.fieldErrors).forEach(([key, errors]) => {
            if (errors?.length) {
              form.setError(key as keyof PrayerUpdateValues, {
                type: 'server',
                message: errors[0],
              });
            }
          });
        }
        setServerError(result.message);
        toast.error(result.message);
      }
    });
  });

  return (
    <Card className="shadow">
      <CardHeader>
        <CardTitle className="text-lg">Í∏∞ÎèÑ Ï†úÎ™© ÏàòÏ†ï</CardTitle>
        <CardDescription>ÎÇ¥Ïö©ÏùÑ ÏàòÏ†ïÌïú Îí§ Ï†ÄÏû•ÌïòÎ©¥ Ï¶âÏãú Î∞òÏòÅÎê©ÎãàÎã§.</CardDescription>
      </CardHeader>
      <CardContent>
        <Form {...form}>
          <form className="space-y-4" onSubmit={handleSubmit}>
            <input type="hidden" {...form.register('id')} />
            <FormField
              control={form.control}
              name="content"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Í∏∞ÎèÑ ÎÇ¥Ïö©</FormLabel>
                  <FormControl>
                    <Textarea placeholder="Ìï®Íªò Í∏∞ÎèÑÌïòÍ≥† Ïã∂ÏùÄ ÎÇ¥Ïö©ÏùÑ ÏûëÏÑ±Ìï¥Ï£ºÏÑ∏Ïöî." className="min-h-[140px]" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="imageUrl"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>ÎåÄÌëú Ïù¥ÎØ∏ÏßÄ (ÏÑ†ÌÉù)</FormLabel>
                  <FormControl>
                    <ImageUploader
                      value={field.value || null}
                      onChange={(url) => field.onChange(url ?? '')}
                      label="Ïù¥ÎØ∏ÏßÄÎäî 5MB Ïù¥ÌïòÏùò ÌååÏùºÎßå ÏóÖÎ°úÎìúÌï† Ïàò ÏûàÏäµÎãàÎã§."
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            {serverError && <p className="text-sm text-destructive">{serverError}</p>}
            <Button type="submit" disabled={isPending} className="w-full">
              {isPending ? 'ÏàòÏ†ï Ï§ë...' : 'Î≥ÄÍ≤Ω ÏÇ¨Ìï≠ Ï†ÄÏû•'}
            </Button>
          </form>
        </Form>
      </CardContent>
      <CardFooter className="text-xs text-muted-foreground">
        Î∏åÎùºÏö∞Ï†Ä ÏûêÎèô Î≤àÏó≠ Í∏∞Îä•ÏùÑ ÌôúÏö©Ìï¥ Îã§Î•∏ Ïñ∏Ïñ¥ ÏÇ¨Ïö©ÏûêÏôÄ Í∏∞ÎèÑÎ•º ÎÇòÎàå Ïàò ÏûàÏäµÎãàÎã§.
      </CardFooter>
    </Card>
  );
}
